@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
.list-container{
    justify-content: center;
    justify-items: center;
    background-color: white;
    overflow: auto;
    display: flex;
    height: calc(80vh );
    border-radius: 20px ;
}
.table{
    width: auto;
    margin: 0;
    padding: 0;
}

.input-container{
    display: flex;
    justify-content: center;
    justify-items: center;
    margin-top: 1vh;
    margin-bottom: 2vh;
}
.input{
    border: 2px solid rgba(0, 0, 255, 0.384) ;
    border-radius: 20px;
    background: white;
    width: 80vh;
    height: 40px;
    padding-left: 2vh;
    padding-right: 2vh;
    font-size: 18px;
    font-weight: 600;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.input:focus{
    outline: none;
}

#root{
    margin: 0;
    padding: 0;
    justify-content: center;
    justify-items: center;
    background-color: #8EC5FC;
background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);


    height: 100vh;
}

.heading{
    font-family: 'Balsamiq Sans', cursive;
    font-weight: 600;
    color: rgba(0, 0, 255, 0.623);
    padding: 1vh;
    justify-content: center;
    text-align: center;
}
